const checkscreenSize = () => {
	let width = window.screen.width;
	let rows = document.querySelectorAll(".flexRow")
	
	if (width <= 450) {
		rows.forEach(row => {
			row.classList.add("flexCol");
			row.classList.remove("flexRow");
			row.classList.remove("flexRowR");
			row.classList.add("flexRowL");
		});
		console.log(width)
	}
}
    
export default checkscreenSize;